.searchbar-container{
    display: flex;
    justify-content: center;
    margin: 0px auto;
    padding: 0 20px;
    align-items: center;
  }
  
  .searchbar{
    background-color: white;
    margin-right: 20px;
  }
  
  .searchbar input{
    padding: 10px;
    box-shadow: 0px 2px 2px rgb(0, 0, 0, 0.25);
    border-radius: 3px;
    border: none;
  }
  
  .searchbar-btn button{
    background-color: #0e6f9f;
    border: none;
    border-radius: 10px;
    height: 44px;
    color: white;
    padding: 10px 12px;
    cursor: pointer;
    transition: 0.1s ease;
  }

  .searchbar-btn button:active{
    opacity: 0.6;
  }